import React, { Component } from 'react';
import { withRouter } from 'react-router';
import moment from 'moment';
import AuthService from '../utils/AuthService';

import InfoBox from '../components/InfoBox.jsx';

const getDescription = (event) => {
  switch (event.type) {
    case 'PERSON_ENTERED':
      return 'Har ankommet Paradise-området.';
    case 'PERSON_LEFT':
      return 'Har forlatt Paradise-området.';
    default:
      return 'Mangler beskrivelse';
  }
};

class Dashboard extends Component {
  constructor(props) {
    super(props);

    this.state = {
      members: [],
      events: [],
      rpiVitals: {}
    };

    this.fetchMembers = this.fetchMembers.bind(this);
    this.fetchEvents = this.fetchEvents.bind(this);
    this.fetchAll = this.fetchAll.bind(this);
    this.fetchRpiVitals = this.fetchRpiVitals.bind(this);
  }

  componentDidMount() {
    this.fetchAll();
    this.fetchIntervalId = setInterval(this.fetchAll, 3000);
  }

  componentWillUnmount() {
    clearInterval(this.fetchIntervalId);
  }

  setStatus(members, events) {
    members.map(member => {
      const lastEvent = events.find(event => event.subject === member.name);
      if (!lastEvent) {
        return Object.assign(member, { status: 'AWAY' });
      }
      return Object.assign(member, {
        status: lastEvent.type === 'PERSON_ENTERED' ? 'PRESENT' : 'AWAY'
      });
    });
    return { members, events };
  }

  fetchAll() {
    this.fetchMembers();
    this.fetchEvents();
    this.fetchRpiVitals();
  }

  fetchMembers() {
    fetch('https://paradise-backend.herokuapp.com/api/members')
    .then(response => response.json())
    .then(members => this.setStatus(members, this.state.events))
    .then(newState => this.setState(newState));
  }

  fetchEvents() {
    fetch('https://paradise-backend.herokuapp.com/api/bluetooth-events')
    .then(response => response.json())
    .then(events => events.map(
      event => Object.assign(event, {
        description: getDescription(event),
        date: moment(event.date)
      })
    ))
    .then(events => events.sort((a, b) => b.date - a.date))
    .then(events => this.setStatus(this.state.members, events))
    .then(newState => this.setState(newState));
  }

  fetchRpiVitals() {
    fetch('https://paradise-backend.herokuapp.com/api/rpi-vitals')
    .then(response => response.json())
    .then(rpiVitals => this.setState({ rpiVitals }))
    .catch(err => console.error(err));
  }

  render() {
    const logout = () => {
      this.props.auth.logout();
      this.props.router.push('/login');
    };

    return (
      <div className="wrapper">
        <header className="main-header">
          <div className="logo">Paradise Fantasy</div>
          <nav className="navbar">
            <div className="navbar-custom-menu">
              <ul className="nav navbar-nav">
                <li className="dropdown user user-menu">
                  <a href="/" className="dropdown-toggle" data-toggle="dropdown">
                    <span>Bruker</span>
                  </a>
                  <ul className="dropdown-menu">
                    <li className="user-footer">
                      <div className="pull-right" onClick={logout}>Logg ut</div>
                    </li>
                  </ul>
                </li>
              </ul>
            </div>
          </nav>
        </header>

        <aside className="main-sidebar">
          <section className="sidebar">
            <ul className="sidebar-menu">
              <li className="header">HEADER</li>
              <li>
                <a href="/">
                  <i className="fa fa-link" />
                  <span>Link?</span>
                </a>
              </li>
            </ul>
          </section>
        </aside>

        <div className="content-wrapper" style={{ minHeight: 'calc(100vh - 101px)' }}>
          <section className="content-header">
            <h1>
              Dashboard
              <small>Version 0.1</small>
            </h1>
          </section>

          <section className="content">
            <div className="row">
              <InfoBox
                color="bg-aqua"
                icon="ion ion-gear-a"
                title="CPU TRAFFIC"
                content={this.state.rpiVitals.load || 'not found'}
              />

              <InfoBox
                color="bg-red"
                icon="glyphicon glyphicon-fire"
                title="Temperature"
                content={this.state.rpiVitals.temperature || 'not found'}
              />

              <InfoBox
                color="bg-green"
                icon="glyphicon glyphicon-oil"
                title="Memory Usage"
                content={`${((this.state.rpiVitals.mem_avail/this.state.rpiVitals.mem_total)*100).toFixed(1)}%` || 'not found'}
              />
              <InfoBox
                color="bg-yellow"
                icon="fa fa-hdd-o"
                title="Disk Usage"
                content={`${((this.state.rpiVitals.disk_avail/this.state.rpiVitals.disk_total)*100).toFixed(1)}%` || 'not found'}
              />

              <InfoBox
                color="bg-yellow"
                icon="ion ion-ios-people-outline"
                title="MEDLEMMER"
                content={`${this.state.members.length}`}
              />
            </div>

            <div className="row">
              <div className="col-md-6 col-sm-12">
                <div className="box box-info">
                  <div className="box-header with-border">
                    <h3 className="box-title">Siste hendelser</h3>
                  </div>
                  <div className="box-body last-events">
                    <div className="table-responsive">
                      <table className="table no-margin">
                        <thead>
                          <tr>
                            <th>Tidspunkt</th>
                            <th>Hvem</th>
                            <th>Hva</th>
                          </tr>
                        </thead>
                        <tbody>
                          {
                            this.state.events.map(event => (
                              <tr key={event.id}>
                                <td>{event.date.format('DD/MM HH:mm:ss')}</td>
                                <td>{event.subject}</td>
                                <td>{event.description}</td>
                              </tr>
                            ))
                          }
                        </tbody>
                      </table>
                    </div>
                  </div>
                  <div className="box-footer clearfix" />
                </div>
              </div>

              <div className="col-md-6 col-sm-12">
                <div className="box box-info">
                  <div className="box-header with-border">
                    <h3 className="box-title">Medlemmer</h3>
                  </div>
                  <div className="box-body">
                    <div className="table-responsive">
                      <table className="table no-margin">
                        <thead>
                          <tr>
                            <th>Navn</th>
                            <th>MAC addresse</th>
                            <th>Farge</th>
                            <th>Rom</th>
                          </tr>
                        </thead>
                        <tbody>
                          {
                            this.state.members.map(member => (
                              <tr key={member.id}>
                                <td>
                                  <i className={`fa fa-user member-status ${member.status}`} />
                                  {member.name}
                                </td>
                                <td>{member.bluetooth_address}</td>
                                <td>{member.color}</td>
                                <td>{member.room}</td>
                              </tr>
                            ))
                          }
                        </tbody>
                      </table>
                    </div>
                  </div>
                  <div className="box-footer clearfix" />
                </div>
              </div>
            </div>
          </section>
        </div>

        <footer className="main-footer">
          <strong>Paradise Fantasy</strong>
        </footer>
      </div>
    );
  }
}

Dashboard.propTypes = {
  auth: React.PropTypes.instanceOf(AuthService),
  router: React.PropTypes.object
};

export default withRouter(Dashboard);



