import React from 'react';
import { Router, Route, IndexRoute, browserHistory } from 'react-router';

import AuthService from './utils/AuthService';

import Main from './views/Main.jsx';
import Dashboard from './views/Dashboard.jsx';
import Login from './views/Login.jsx';

const auth = new AuthService('b3Eo2rn8uwa4HwvnPyphOU4kzmIwfIej', 'elddev.eu.auth0.com');

// onEnter callback to validate authentication in private routes
const requireAuth = (nextState, replace) => {
  console.log('requiresAuth');
  if (!auth.loggedIn()) {
    replace({ pathname: '/login' });
  }
};

const requireLogout = (nextState, replace) => {
  console.log('requiresLogout?');
  if (auth.loggedIn()) replace({ pathname: '/' });
};

const App = () => (
  <Router history={browserHistory}>
    <Route path="/" component={Main} auth={auth}>
      <IndexRoute component={Dashboard} />
      <Route path="/login" component={Login} onEnter={requireLogout} />
      <Route path="#access_token=:token" component={Login} />
    </Route>
  </Router>
);

export default App;



