import React from 'react';
import AuthService from '../utils/AuthService';

const Login = (props) => {
  const login = props.auth.login.bind(this);
  return (
    <div className="login-view full-width-height">
      <div>
        <h1>Velkommen til Paradise</h1>
        <button className="btn btn-primary btn-lg" onClick={login}>Logg inn</button>
      </div>
    </div>
  );
};

Login.propTypes = {
  auth: React.PropTypes.instanceOf(AuthService)
};

export default Login;



