import React from 'react';

const Main = ({ children, route }) => {
  const childrenWithAuth = React.cloneElement(children, {
    auth: route.auth // sends auth instance from route to children
  });

  return (
    <div className="full-width-height skin-blue">
      {childrenWithAuth}
    </div>
  );
};

Main.propTypes = {
  children: React.PropTypes.element.isRequired,
  route: React.PropTypes.object
};

export default Main;



